import { graphql } from "gatsby";
import React from "react";
import PostLink from "../components/post-link";
import * as styles from "./style.module.css";

const DiariesIndexPage = ({
  data: {
    allMarkdownRemark: { edges },
  },
}) => {
  const posts = {};
  edges
    .filter((edge) => !!edge.node.frontmatter.date)
    .forEach((edge) => {
      const month = edge.node.frontmatter.date.substring(0, 7);
      const monthDiary = posts[month] || [];
      monthDiary.push(<PostLink key={edge.node.id} post={edge.node} />);
      posts[month] = monthDiary;
    });

  return (
    <div className={styles.diary}>
      <div>
        <h1>噜啦写字的地方</h1>
        <hr />
        <div>
          {Object.keys(posts).map((month) => {
            return (
              <div>
                <h2>{month}</h2>
                {posts[month]}
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default DiariesIndexPage;

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { frontmatter: { date: ASC } }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "YYYY-MM-DD")
            slug
            title
          }
        }
      }
    }
  }
`;
